import i18n from '@dhis2/d2-i18n';
import getCurrentDate from './get-current-date.js';
export const startYear = 1970; // Period types

export const DAILY = 'Daily';
export const WEEKLY = 'Weekly';
export const WEEKLY_WEDNESDAY = 'WeeklyWednesday';
export const WEEKLY_THURSDAY = 'WeeklyThursday';
export const WEEKLY_SATURDAY = 'WeeklySaturday';
export const WEEKLY_SUNDAY = 'WeeklySunday';
export const BI_WEEKLY = 'BiWeekly';
export const MONTHLY = 'Monthly';
export const BI_MONTHLY = 'BiMonthly';
export const QUARTERLY = 'Quarterly';
export const SIX_MONTHLY = 'SixMonthly';
export const SIX_MONTHLY_APRIL = 'SixMonthlyApril';
export const SIX_MONTHLY_NOV = 'SixMonthlyNov';
export const YEARLY = 'Yearly';
export const FINANCIAL_APRIL = 'FinancialApril';
export const FINANCIAL_JULY = 'FinancialJuly';
export const FINANCIAL_OCT = 'FinancialOct';
export const FINANCIAL_NOV = 'FinancialNov';
export function getYearlyPeriodIdForTypeAndYear(type, year) {
  const yearStr = year.toString();

  switch (type) {
    case YEARLY:
      return yearStr;

    case FINANCIAL_APRIL:
      return "".concat(year, "April");

    case FINANCIAL_JULY:
      return "".concat(year, "July");

    case FINANCIAL_OCT:
      return "".concat(year, "Oct");

    case FINANCIAL_NOV:
      return "".concat(year, "Nov");

    default:
      throw new Error("Period type \"".concat(type, "\" is not a yearly type"));
  }
}
export const createUpdateFullPeriodTimeToDate = multiplier => {
  return (date, periodType) => {
    // Clone the date object, to not mutate the original
    const updatedDate = new Date(date);

    switch (periodType) {
      case DAILY:
        updatedDate.setDate(updatedDate.getDate() + 1 * multiplier);
        break;

      case WEEKLY:
      case WEEKLY_WEDNESDAY:
      case WEEKLY_THURSDAY:
      case WEEKLY_SATURDAY:
      case WEEKLY_SUNDAY:
        updatedDate.setDate(updatedDate.getDate() + 7 * multiplier);
        break;

      case BI_WEEKLY:
        updatedDate.setDate(updatedDate.getDate() + 14 * multiplier);
        break;

      case MONTHLY:
        updatedDate.setMonth(updatedDate.getMonth() + 1 * multiplier);
        break;

      case BI_MONTHLY:
        updatedDate.setMonth(updatedDate.getMonth() + 2 * multiplier);
        break;

      case QUARTERLY:
        updatedDate.setMonth(updatedDate.getMonth() + 3 * multiplier);
        break;

      case SIX_MONTHLY:
      case SIX_MONTHLY_APRIL:
      case SIX_MONTHLY_NOV:
        updatedDate.setMonth(updatedDate.getMonth() + 6 * multiplier);
        break;

      case YEARLY:
      case FINANCIAL_APRIL:
      case FINANCIAL_JULY:
      case FINANCIAL_OCT:
      case FINANCIAL_NOV:
        updatedDate.setFullYear(updatedDate.getFullYear() + 1 * multiplier);
        break;
    }

    return updatedDate;
  };
};
const monthlyCounts = {
  [MONTHLY]: 1,
  [BI_MONTHLY]: 2,
  [QUARTERLY]: 3,
  [SIX_MONTHLY]: 6,
  [SIX_MONTHLY_APRIL]: 6,
  [SIX_MONTHLY_NOV]: 6
};
const monthlyPeriodTypes = Object.keys(monthlyCounts);

const getPreviousMonthNumber = (startMonth, periodType) => {
  const monthsToRemove = monthlyCounts[periodType];
  const monthDifference = startMonth - monthsToRemove;

  if (monthDifference < 0) {
    return monthDifference + 12;
  }

  return monthDifference;
};

export const addFullPeriodTimeToDate = createUpdateFullPeriodTimeToDate(1);
export const removeFullPeriodTimeToDate = (originalDate, periodType) => {
  const naiveRemoveFullPeriodTimeToDate = createUpdateFullPeriodTimeToDate(-1);
  const updatedDate = naiveRemoveFullPeriodTimeToDate(originalDate, periodType);

  if (monthlyPeriodTypes.includes(periodType)) {
    while (updatedDate.getMonth() !== getPreviousMonthNumber(originalDate.getMonth(), periodType)) {
      updatedDate.setDate(updatedDate.getDate() - 1);
    }
  }

  return updatedDate;
};
